import "./css/about-card.css";
const AboutCard = () => {
  return (
    <div className="about-card-con">
      <p className="img"></p>
      <p className="name">
        <div>Vikas Saini</div>
        <div style={{ color: "white", fontSize: "0.7rem", opacity: "0.4" }}>
          Ceo Founder Darkroom Inc
        </div>
      </p>
      <p className="social">
        <li>
          <i class="fab fa-instagram"></i>
        </li>
        <li>
          <i class="fab fa-twitter"></i>
        </li>
        <li>
          <i class="fab fa-facebook"></i>
        </li>
      </p>
    </div>
  );
};
export default AboutCard;
