import { Link } from "react-router-dom";
import "./project-inner.css";
const ProjectInner = (props) => {
  return (
    <div className="main-content project-inner">
      <h1 className="heading">
        <Link to="/projects">
          <span className="back-btn">
            <i class="fas fa-chevron-left"></i>
          </span>
        </Link>
        {props.name}
      </h1>
      <div className="project-external-links">
        <li>
          <a target="_blank" href={props.githubUrl}>
            <i class="fab fa-github "></i>
          </a>
        </li>
        <li>
          <a target="_blank" href={props.githubUrl}>
            <i class="fab fa-github "></i>
          </a>
        </li>
        <li>
          <a target="_blank" href={props.githubUrl}>
            <i class="fab fa-github "></i>
          </a>
        </li>
      </div>
      <br />
      <a target="_blank" href={props.url}>
        <button className="live-site-btn">
          Feel Live &nbsp;&nbsp;&nbsp; <i class="fas fa-external-link-alt"></i>
        </button>
      </a>
      <div className="about-project">
        <h4>Tech used : </h4>
      </div>
      {/* Tech used Mapping */}
      <div className="tech-used">
        {props.frontendTechUsed.map((techName) => (
          <li>{techName}</li>
        ))}
      </div>
    </div>
  );
};
export default ProjectInner;
