import { Link } from "react-router-dom";
import "./css/nav.css";

import AboutCard from "./AboutCard";
const Nav = () => {
  return (
    <div className="nav-con">
      <AboutCard />
      <div className="nav-list">
        <Link to="/projects">
          <li>
            <i class="fas fa-project-diagram li-icon"></i> Projects
          </li>
        </Link>

        <Link to="/categories">
          <li>
            <i class="fas fa-boxes li-icon"></i> Categories
          </li>
        </Link>

        <Link to="/skills">
          <li
          //  className="nav-active"
          >
            <i class="fas fa-vials li-icon"></i> Skills
          </li>
        </Link>

        <Link to="/contact">
          <li>
            <i class="fas fa-id-card-alt li-icon"></i> Contact
          </li>
        </Link>

        <Link to="/about">
          <li>
            <i class="fas fa-info-circle li-icon"></i> About
          </li>
        </Link>
      </div>
    </div>
  );
};
export default Nav;
